.banner-wrapper {
    margin: 10vw 0 15vw 0;
}

.banner-skew-div {
    transform: skew(0, -6deg);
    padding: 0px;
    overflow: hidden;
    border: 10px solid;
    border-width: 10px 0;
    background-color: #1a1a1a;
    box-shadow: 0 0 20px red;
    animation:  border-color-cycle 10s linear 0s infinite normal none, 
                shadow-color-cycle 10s ease-in-out 0s infinite normal none;
}

.banner-tag {
    transform: skew(0, -6deg);
    color:white;
    font-family: 'Cutive Mono', monospace;
    font-size: 4vw;
}

.banner-content-wrapper {
    position: relative;
    display: inline-block;
    width: 90%;
    padding-bottom: 41%;
}

.banner-img-wrapper {
    display: inline-block;
    vertical-align: top;
    padding: 0 20px;
    width: 50%;
    top: -110px;
    box-sizing: border-box;
}

.banner-text-wrapper {
    display: inline-block;
    vertical-align: top;
    padding: 0 20px;
    width: 50%;
    box-sizing: border-box;
}

.banner-content-img {
    transform: skew(0, 6deg);
    width: 100%;
    clip-path: polygon(0 10.5%, 100% 0, 100% 89.5%, 0 100%);
}

.banner-header {
    margin-top: .4em;
}

.banner-content-text {
    padding: 20px;
}

.banner-project-link {
    display: table;
    margin: auto;
    text-decoration: none;
    font-weight: bold;
    color: red;
    letter-spacing: 4px;
    animation: text-color-cycle 10s linear 0s infinite normal none;
}

.banner-project-link:hover {
    text-decoration: underline;
}

.banner-button {
    display: inline-block;
    height: 100%;
    width: 5%;
    user-select: none;
}

.banner-svg {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    height: 30%;
    stroke-linecap: round;
    stroke-width: 6px;
    fill-opacity: 0;
    stroke: red;
    animation:  stroke-color-cycle 10s linear 0s infinite normal none,
                drop-shadow-color-cycle 10s linear 0s infinite normal none;
}

.banner-svg:hover {
    cursor: pointer;
}

.banner-motion {
    position: absolute;
}

@keyframes border-color-cycle {
    0%   {border-color: #fcc;}
    16%  {border-color: #ffc;}
    33%  {border-color: #cfc;}
    50%  {border-color: #cff;}
    66%  {border-color: #ccf;}
    87%  {border-color: #fcf;}
    100% {border-color: #fcc;}
}

@keyframes background-color-cycle {
    0%   {background-color: #fcc;}
    16%  {background-color: #ffc;}
    33%  {background-color: #cfc;}
    50%  {background-color: #cff;}
    66%  {background-color: #ccf;}
    87%  {background-color: #fcf;}
    100% {background-color: #fcc;}
}

@keyframes shadow-color-cycle {
    0%   {box-shadow: 0 0 20px #f00;}
    16%  {box-shadow: 0 0 25px #ff0;}
    33%  {box-shadow: 0 0 20px #0f0;}
    50%  {box-shadow: 0 0 25px #0ff;}
    66%  {box-shadow: 0 0 20px #00f;}
    87%  {box-shadow: 0 0 25px #f0f;}
    100% {box-shadow: 0 0 20px #f00;}
}

@keyframes text-color-cycle {
    0%   {color: #faa;}
    16%  {color: #ffa;}
    33%  {color: #afa;}
    50%  {color: #aff;}
    66%  {color: #aaf;}
    87%  {color: #faf;}
    100% {color: #faa;}
}

@keyframes stroke-color-cycle {
    0%   {stroke: #fcc;}
    16%  {stroke: #ffc;}
    33%  {stroke: #cfc;}
    50%  {stroke: #cff;}
    66%  {stroke: #ccf;}
    87%  {stroke: #fcf;}
    100% {stroke: #fcc;}
}

@keyframes drop-shadow-color-cycle {
    0%   {filter: drop-shadow(0 0 5px #f00);}
    16%  {filter: drop-shadow(0 0 5px #ff0);}
    33%  {filter: drop-shadow(0 0 5px #0f0);}
    50%  {filter: drop-shadow(0 0 5px #0ff);}
    66%  {filter: drop-shadow(0 0 5px #00f);}
    87%  {filter: drop-shadow(0 0 5px #f0f);}
    100% {filter: drop-shadow(0 0 5px #f00);}
}

@keyframes underline-color-cycle {
    0%   {text-decoration: underline #faa;}
    16%  {text-decoration: underline #ffa;}
    33%  {text-decoration: underline #afa;}
    50%  {text-decoration: underline #aff;}
    66%  {text-decoration: underline #aaf;}
    87%  {text-decoration: underline #faf;}
    100% {text-decoration: underline #faa;}
}