body {
    background-color: #121212;
    margin: 0;
}

/* content div */

@media (min-width: 576px) {
    .content-container {
        max-width: 540px;
        margin: 0 auto;
    }

    .scale-text-header {
        font-size: 20px;
    }

    .scale-text-body {
        font-size: 10px;
    }
}

@media (min-width: 992px) {
    .content-container {
        max-width: 960px;
        margin: 0 auto;
    }

    .scale-text-header {
        font-size: 40px;
    }

    .scale-text-body {
        font-size: 20px;
    }
}

@media (min-width: 1500px) {
    .content-container {
        max-width: 1240px;
        margin: 0 auto;
    }

    .scale-text-header {
        font-size: 50px;
    }

    .scale-text-body {
        font-size: 25px;
    }
}

.section-header {
    margin: 0 auto;
    color:white;
    font-family: 'Cutive Mono', monospace;
    font-size: 4vw;
    width: max-content;
    margin-bottom: 0;
}

.header-underline {
    height: 6px;
    animation:  background-color-cycle 10s linear 0s infinite normal none,
                shadow-color-cycle 10s linear 0s infinite normal none;
}

.text-header {
    margin-bottom: 0;
    font-family: 'Cutive Mono', monospace;
    color:white;
}

.text-body {
    color:white;
    font-family: 'Roboto', sans-serif;
}