.splash-container {
    position: relative;
    width: 100%;
    height: 50vw;
}

.splash-canvas {
    width: 100%;
}

.splash-text-container {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    padding: 0 11vw;
}

.splash-text-container h1 {
    margin: 10px 0;
    font-size: 6vw;
    color:#fff;
    font-family: 'Cutive Mono', monospace;
}

.splash-text-container h2 {
    margin-top: 0;
    font-size: 2.5vw;
    color:#fff;
    font-family: 'Roboto', sans-serif;
    font-weight: 100;
    font-style: italic;
}