.card-container {
    position: relative;
    display: inline-block;
    width: 50%;
    padding: 5%;
    margin-bottom: 50px;
    transform: skewY(5deg);
    transform-origin: left;
    box-sizing: border-box;
    vertical-align: top;
}

.card-wrapper {
    position: relative;
    float: right;
    background-color: #1a1a1a;
    border: 7px solid red;
    overflow: hidden;
    animation:  border-color-cycle 10s linear 0s infinite normal none, 
                shadow-color-cycle 10s ease-in-out 0s infinite normal none;
}

.card-content {
    transform: skewY(-5deg);
    transform-origin: left;
}

.card-img {
    position: relative;
    width: 100%;
    vertical-align: top;
    z-index: -1;
}

.card-link {
    width: 100%;
    text-decoration: none;
    z-index: 100;
    margin: 0 !important;
}

.card-link p {
    margin: 0;
    text-align: center;
    font-weight: bolder;
    letter-spacing: 4px;
    padding: 8px;
    background-color: #2a2a2a;
    color: #1a1a1a;
    box-shadow: 0 0 20px #111;
    animation: text-color-cycle 10s linear 0s infinite normal none;
}

.card-text-content {
    padding: 20px 20px 0 20px;
}

.card-header {
    position: static;
    float: right;
}

.card-text-content p {
    margin-top: 0;
}

.card-header h2 {
    margin: 0;
}

.card-footer {
    position: absolute;
    color:#aaa;
    font-family: 'Cutive Mono', monospace;
    font-style: italic;
    right: 6%;
    bottom: -8%;
}